import React, { useEffect, useState } from 'react'
import ConfigByDefault, {
  displayByDefault,
  explorerType
} from './ConfigByDefault'
import NavCard from './NavCard'
import Style from './carte.module.css'
import configByDefault from './ConfigByDefault'
import BlockCard from './BlockCard'
import BookMarkCard from './BookMarkCard'

function ExplorerDisplay({
  loadFunction,
  loadElementsById,
  userId,
  createBookMarkData,
  loadBookMark,
  deleteBookMark,
  buttonsList,
  readValidated,
  resources
}) {
  configByDefault.resources = resources
  const dbf = { ...displayByDefault }
  dbf.titles = [
    resources !== undefined ? resources.firstTitle : '',
    resources !== undefined ? resources.secondTitle : '',
    resources !== undefined ? resources.thirdTitle : '',
    resources !== undefined ? resources.fourthTitle : ''
  ]
  configByDefault.displayByDefault = { ...dbf }
  const [Content, setContent] = useState(
    ConfigByDefault.general.displayAllCard
      ? new Array(ConfigByDefault.general.cardNumber)
      : new Array(1)
  )

  const [Block, setBlock] = useState([])
  const [load, setLoader] = useState(false)
  const [bookMark, setBookMark] = useState({
    universe: '',
    book: '',
    chapter: '',
    scene: '',
    user: userId,
    title: ''
  })
  const [BookMarkList, setBookMarkList] = useState([])
  console.log('Content Start of Component')
  console.log(Content)
  console.log(Block)
  useEffect(() => {
    const test = () => {
      console.log('Test')
      if (ConfigByDefault.general.displayAllCard) {
        let c = [...Content]
        for (let i = 0; i < ConfigByDefault.general.cardNumber; i++) {
          c[i] = []
        }
        console.log('c')
        console.log(c)
        setContent([...c])
      }
      console.log('c')
      console.log(Content)
      navigation(-1, null)
      console.log('AfterNav')
      setLoader(true)
    }
    const getData = async () => {
      if (ConfigByDefault.general.loadAll) {
        await loadFunction((data) =>
          ConfigByDefault.functions.loadAll(data, () => test())
        )
      } else {
        navigation(-1, null)
        setLoader(true)
      }

      await loadBookMark((data) => {
        console.log('Data BookMark')
        console.log(data)
        setBookMarkList(data.bookmarks)
      })
    }
    getData()
  }, [])
  const navigation = (niveau, id) => {
    console.log(niveau + ' : ' + id)
    let bm = { ...bookMark }
    console.log('bm')
    switch (niveau) {
      case 0:
        bm.universe = id
        break
      case 1:
        bm.book = id
        break
      case 2:
        bm.chapter = id
        break
      case 3:
        bm.scene = id
        break
      case -1:
        console.log('First Call')
        break
      default:
        console.error('Error to BookMark Generation !')
    }
    setBookMark({ ...bm })
    let c = [...Content]
    console.log('Content')
    console.log(c)
    console.log('Config By Default')
    console.log(configByDefault)
    if (ConfigByDefault.general.displayAllCard) {
      console.log('All Card')
      for (let i = c.length; i > niveau + 1; i--) {
        c[i - 1] = []
      }
    } else {
      if (niveau + 1 < ConfigByDefault.general.cardNumber) {
        for (let i = c.length; i > niveau + 1; i--) {
          console.log('pop')
          c.pop()
        }
      }
    }
    if (configByDefault.general.loadAll) {
      if (configByDefault.general.displayAllCard) {
        if (niveau + 1 < ConfigByDefault.general.cardNumber) {
          console.log('Element')
          console.log(configByDefault.content)
          console.log(id)
          c[niveau + 1] = ConfigByDefault.content.filter((x) => x.parent === id)

          console.log('Element')
          console.log(c)
          setBlock([])
        } else {
          console.log('Block')

          setBlock([
            ...ConfigByDefault.displayContent.filter((x) => x.parent === id)
          ])
        }
      } else {
        if (niveau + 1 < ConfigByDefault.general.cardNumber) {
          c.push(ConfigByDefault.content.filter((x) => x.parent === id))
          console.log('Loading Cards')
          console.log(c)
          setBlock([])
        } else {
          setBlock([
            ...ConfigByDefault.displayContent.filter((x) => x.parent === id)
          ])
        }
      }
      console.log('Fin Nav')
      console.log(c)
      setContent([...c])
    } else {
      //if (configByDefault.general.displayAllCard) {
      /*if (niveau === -1) {
          loadElementsById(
            (data) => {
              configByDefault.functions.loadContentById(
                data,
                c,
                setContent,
                null,
                niveau,
                (d, c, setContent, setBlock, niveau) => {
                  c[niveau + 1] = d.Elements
                  setContent([...c]) 
                }
              )
            },
            niveau,
            id
          )
        } else {*/
      console.log('Second load')
      loadElementsById(
        (data) => {
          configByDefault.functions.loadContentById(
            data,
            c,
            setContent,
            setBlock,
            niveau,
            configByDefault.general.displayAllCard,
            configByDefault.general.cardNumber,
            (d, c, setContent, setBlock, niveau, displayall, cardNumber) => {
              if (displayall) {
                if (niveau + 1 < c.length) {
                  c[niveau + 1] = d.Elements
                  setBlock([])
                  setContent([...c])
                } else {
                  setBlock([...d.Blocks])
                }
              } else {
                if (niveau + 1 < cardNumber) {
                  c.push([...d.Elements])
                  setBlock([])
                  setContent([...c])
                } else {
                  setBlock([...d.Blocks])
                }
              }
            }
          )
        },
        niveau,
        niveau === -1 ? 'Root' : id
      )
      //}
      //}
    }
  }
  const createBookMark = () => {
    createBookMarkData(bookMark)
  }
  const updateBookMark = (id, title) => {
    createBookMarkData({ ...bookMark, id, title })
  }
  const readValidatedScene = () => {
    readValidated(bookMark)
  }
  if (load) {
    console.log(Content)
  }
  const bmNavigation = (u, b, c, s) => {
    alert(u)
    NavigationWithBookMark(u, b, c, s)
  }
  const NavigationWithBookMark = (u, b, ch, s) => {
    let bm = { ...bookMark }
    let c = [...Content]
    for (
      let niveau = 0;
      niveau <= configByDefault.general.cardNumber - 1;
      niveau++
    ) {
      let id = ''
      switch (niveau) {
        case 0:
          bm.universe = u
          id = u
          break
        case 1:
          bm.book = b
          id = b
          break
        case 2:
          bm.chapter = ch
          id = ch
          break
        case 3:
          bm.scene = s
          id = s
          break
        case -1:
          break
        default:
          console.error('Error to BookMark Generation !')
      }

      if (ConfigByDefault.general.displayAllCard) {
        for (let i = c.length; i > niveau + 1; i--) {
          c[i - 1] = []
        }
        if (niveau + 1 < ConfigByDefault.general.cardNumber) {
          c[niveau + 1] = ConfigByDefault.content.filter((x) => x.parent === id)
          setBlock([])
        } else {
          setBlock([
            ...ConfigByDefault.displayContent.filter((x) => x.parent === id)
          ])
        }
      } else {
        if (niveau + 1 < ConfigByDefault.general.cardNumber) {
          for (let i = c.length; i > niveau + 1; i--) {
            console.log('pop')
            c.pop()
          }
          c.push(ConfigByDefault.content.filter((x) => x.parent === id))
          console.log('Loading Cards')
          console.log(c)
          setBlock([])
        } else {
          setBlock([
            ...ConfigByDefault.displayContent.filter((x) => x.parent === id)
          ])
        }
      }
    }
    console.log(c)
    setBookMark({ ...bm })
    setContent([...c])
  }
  return (
    <div>
      <div className={Style.displayCards}>
        <BookMarkCard
          bookMarks={BookMarkList}
          bookMarkNavigation={(u, b, c, s) => bmNavigation(u, b, c, s)}
          deleteBookMark={(id) => {
            deleteBookMark(id)
            let b = [...BookMarkList]
            b = b.filter((x) => x.id != id)
            setBookMarkList([...b])
          }}
          ButtonsList={buttonsList}
          resources={resources}
        />
        {load
          ? Content.map((c, index) => {
              console.log(index)
              console.log(Content[index])
              return (
                <NavCard
                  key={index}
                  niveau={index}
                  content={Content[index]}
                  general={ConfigByDefault.general}
                  specificity={
                    (ConfigByDefault.type = explorerType.explorer
                      ? ConfigByDefault.explorer
                      : ConfigByDefault.questionnary)
                  }
                  display={ConfigByDefault.displayByDefault}
                  navigation={(niveau, id) => navigation(niveau, id)}
                  title={configByDefault.displayByDefault.titles[index]}
                  idSelected={bookMark}
                />
              )
            })
          : null}
      </div>
      {Block.length > 0 && Block[0] !== undefined ? (
        <div className={Style.displayCards}>
          <BlockCard
            blocks={Block}
            setBookMark={setBookMark}
            createBookMark={() => createBookMark()}
            updateBookMark={(id, title) => updateBookMark(id, title)}
            bookMark={bookMark}
            bookMarkList={BookMarkList}
            ButtonsList={buttonsList}
            readValidated={() => readValidatedScene()}
          />
        </div>
      ) : null}
    </div>
  )
}

export default ExplorerDisplay
