import React from 'react'
import ExplorerDisplay from './ExplorerDisplay'
import styles from './styles.module.css'
import {
  explorerType,
  generalByDefault,
  explorerDefault,
  questionnaryDefault,
  functions,
  contentByDefault,
  displayByDefault,
  displayContentByDefault
} from './ConfigByDefault'

export const Explorer = ({
  load,
  loadElementsById,
  userId,
  createBookMark,
  loadBookMark,
  deleteBookMark,
  buttonsList,
  readValidated,
  resources
}) => {
  return (
    <ExplorerDisplay
      loadFunction={(d) => load(d)}
      loadElementsById={(data, niveau, id) =>
        loadElementsById(data, niveau, id)
      }
      userId={userId}
      createBookMarkData={createBookMark}
      loadBookMark={(data) => loadBookMark(data)}
      deleteBookMark={(id) => deleteBookMark(id)}
      buttonsList={buttonsList}
      readValidated={(data) => readValidated(data)}
      resources={resources}
    />
  )
}

export const typeExplorer = explorerType

export const defaultGeneral = generalByDefault

export const defaultExplorer = explorerDefault

export const defaultQuestionnary = questionnaryDefault

export const defaultFunctions = functions

export const defaultContent = contentByDefault

export const defaultDisplay = displayByDefault

export const defaultDisplayContent = displayContentByDefault
