import React, { useState } from 'react'
import Style from './carte.module.css'

export default function BlockCard({
  blocks,
  bookMark,
  setBookMark,
  createBookMark,
  bookMarkList,
  updateBookMark,
  ButtonsList,
  readValidated
}) {
  const [title, seTitle] = useState('')
  const [idBm, setIdBm] = useState('')
  console.log('Blocks')
  console.log(blocks)
  return (
    <div id={Style.articleBlock}>
      <div className={Style.w100}>
        <div className={Style.enveloppe}>
          {blocks
            .sort((a, b) => {
              return a.order - b.order
            })
            .map((b, index) => {
              return (
                <div
                  key={index}
                  style={{
                    padding:
                      b.text.substring(0, 3).indexOf('-') !== -1
                        ? '3px 10px 2px 20px'
                        : '3px 10px 2px 5px'
                  }}
                >
                  {b.text}
                </div>
              )
            })}
          <div className={Style.cardFooter}>
            <div className={Style.buttonFooter}>
              <input
                type='text'
                onInput={(e) => {
                  let bm = { ...bookMark }
                  bm.title = e.target.value
                  setBookMark({ ...bm })
                }}
                placeholder={'Name of New BookMark'}
              />
              {ButtonsList.createBM('Create Bookmark', () =>
                bookMark.title != '' ? createBookMark() : alert('Entrez un nom')
              )}
            </div>
            <div className={Style.buttonFooter}>
              {bookMarkList.length > 0 ? (
                <div className={Style.editBM}>
                  <select
                    onChange={(e) => {
                      setIdBm(e.target.value)
                      const t = bookMarkList.filter(
                        (x) => x.id === e.target.value
                      )[0].title
                      seTitle(t)
                    }}
                  >
                    {bookMarkList.map((bm, index) => {
                      return (
                        <option key={index} value={bm.id}>
                          {bm.title}
                        </option>
                      )
                    })}
                  </select>
                  {ButtonsList.updateBM('Update BookMark', () =>
                    updateBookMark(idBm, title)
                  )}
                </div>
              ) : null}
            </div>
            <div className={Style.buttonFooter}>
              {ButtonsList.readValidated('Mark as Read', () => readValidated())}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
