import React from 'react'
import Style from './carte.module.css'

export default function BookMarkCard({
  bookMarks,
  bookMarkNavigation,
  deleteBookMark,
  ButtonsList,
  resources
}) {
  console.log(bookMarks)
  return (
    <div className={Style.article}>
      <div className={Style.w100}>
        <h3>{resources.Bookmark}</h3>
        <div className={Style.enveloppe}>
          {bookMarks.length > 0 ? (
            bookMarks.map((bm, index) => {
              return (
                <div key={index} className={Style.displayinfo}>
                  <button
                    onClick={() => {
                      console.log(
                        bm.universe +
                          ' : ' +
                          bm.book +
                          ' : ' +
                          bm.chapter +
                          ' : ' +
                          bm.scene
                      )
                      bookMarkNavigation(
                        bm.universe,
                        bm.book,
                        bm.chapter,
                        bm.scene
                      )
                    }}
                  >
                    {bm.title}
                  </button>
                  {ButtonsList.deleteBM('Delete', () => deleteBookMark(bm.id))}
                </div>
              )
            })
          ) : (
            <div>{resources.createBookMark}</div>
          )}
        </div>
      </div>
    </div>
  )
}
