import React, { useState } from 'react'
import Style from './carte.module.css'

export default function NavCard({
  niveau,
  content,
  general,
  specificity,
  display,
  navigation,
  title,
  idSelected
}) {
  console.log(content)
  return (
    <div className={Style.article}>
      <div className={Style.w100}>
        <h3>{title}</h3>
        <div className={Style.enveloppe}>
          {content
            .sort((a, b) => {
              return a.numero - b.numero
            })
            .map((c, index) => {
              return (
                <button
                  key={index}
                  style={{
                    ...display.colorTitle[c.status],
                    ...{
                      border: Object.values(idSelected).includes(c.id)
                        ? '4px solid darkgray'
                        : 'none'
                    }
                  }}
                  className={Style.displayinfo}
                  onClick={() => {
                    navigation(niveau, c.id)
                  }}
                >
                  {c.title}
                </button>
              )
            })}
        </div>
      </div>
    </div>
  )
}
